import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { QuickTransferJourneyConfiguration } from '../../models/config.model';
import { Arrangement } from '../../models/items.model';

@Component({
  selector: 'bb-quick-transfer-accounts-ui',
  templateUrl: './quick-transfer-accounts.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class QuickTransferAccountsComponent {
  /**
   * Arrangements that are in the type of Account or Contact or ExternalAccount
   */
  @Input() arrangements: Arrangement[] | undefined;
  /**
   * Selected account to apply "selected" css
   */
  @Input() selected: string | undefined;
  /**
   * Id of account that is selected at the other account selector
   */
  @Input() selectedCounterPartyAccount: string | undefined;
  /**
   * Quick Transfer configuration
   */
  @Input() config?: QuickTransferJourneyConfiguration;
  /**
   * Output to inform which account or contact is selected
   */
  @Output() select = new EventEmitter<string>();
  /**
   * Emit event to inform parent with the picked account id
   *
   * @param id
   */
  pick(id: string) {
    this.select.emit(id);
  }
}
